import {useEffect, useState} from "react";
import LanguageBar from "../components/HomeComponents/topbar/languagebar";
import NavigationBar from "../components/HomeComponents/topbar/navigationbar";
import MetaTags from "react-meta-tags";
import Footer from "../components/footer";
import {Link, useNavigate, useParams} from "react-router-dom";
import Loader from "../components/Loader";
import useSWR from "swr";
import {useTranslation} from "react-i18next";
import {MdArrowBack,} from "react-icons/md";
import FindAProperty from "../components/HomeComponents/FindAProperty";

const V360 = () => {

  const { t } = useTranslation();

  const navigate = useNavigate();

  const { id } = useParams();

  const [feat, setFeats] = useState([])

  const [loader, setLoader] = useState(true);

  const [data, setData] = useState({});

  const {
    isLoading,
    data: axiosData,
    error: axiosErr,
  } = useSWR(`/api/qsyrys/property/${id}`);

  useEffect(() => {
    if (axiosData) {
      const { property, search, featured } = axiosData;

      setData(property);

      setFeats(featured ? featured.slice(0, 10) : []);

    }


    if (axiosErr && !Object.keys(data).length) {
        console.log(axiosErr, 'ss')

        const err = ["Network Error", "timeout exceeded"];

        if (err.includes(axiosErr.message)) {
            navigate('/408')
        }else{
            navigate("/404");
        }
    }
  }, [axiosData, axiosErr]);

  useEffect(() => {
    if (Object.keys(data).length) {
      setLoader(false);
    }

  }, [data]);

    document.oncontextmenu = (e) => {
        e.preventDefault();
        return false;
    };
    const iframe = document.querySelector("iframe html");
    if (iframe) {
        console.log('coool')
        iframe.oncontextmenu = (e) => {
            e.preventDefault();
            return false
        }
}

  return (
    <>
      <MetaTags>
        <title>
          {data?.name || "Syrys"} | Virtual Tour | Property Investment
        </title>
      </MetaTags>

      <iframe
        // src={data.virtualtour}
        src={"https://storage.net-fs.com/hosting/7917334/2/"}
        name="SYRYS INVESTMENT LTD"
        width="100%"
        frameborder="0"
        allow="fullscreen; accelerometer; gyroscope; magnetometer; vr; xr; xr-spatial-tracking; autoplay; camera; microphone"
        allowfullscreen="true"
        webkitallowfullscreen="true"
        mozallowfullscreen="true"
        id="virtual"
        className="virtual"
        style={{
          height: "calc(100vh - 240px)",
        }}
        oallowfullscreen="true"
        msallowfullscreen="true"
      ></iframe>
      <div
        className="home page-template page-template-elementor_header_footer elementor-kit-4695 page page-id-58 wp-embed-responsive beyot-class chrome header-is-float header-sticky-fix-style elementor-default elementor-template-full-width elementor-kit-4695 elementor-page elementor-page-58 e--ua-blink e--ua-chrome e--ua-webkit elementor elementor-58 property-template-default single single-property postid-8381 has-sidebar"
        id="wrapper"
      >
        <Loader deactivate={!loader} />

        {!loader && (
          <>
            <LanguageBar />
            <NavigationBar />

            <div id="wrapper-content" className="clearfix ">
              <div
                id="primary-content"
                className="!pt-[79px] md3:!pt-0 pd-bottom-0"
              >
                <div className="property-gallery-item min-h-[calc(100vh-200px)] flex items-center justify-start ere-light-gallery flex-col">
                  <Link
                    to={`/property/${id}`}
                    className="h-[50px] flex items-center w-full px-[15px]"
                  >
                    <MdArrowBack
                      color={"rgb(32,33,36)"}
                      className="relative mr-1"
                      size={20}
                    />

                    <span className="capitalize text-[14px] block ml-1">
                      {t("go_back_overview")}
                    </span>
                  </Link>
                  {data.virtualtour ? (
                    <iframe
                      src={data.virtualtour}
                      //   src={"https://www.youtube.com/embed/S317UPp_0Bw"}
                      name="SYRYS INVESTMENT LTD"
                      width="100%"
                      frameborder="0"
                      allow="fullscreen; accelerometer; gyroscope; magnetometer; vr; xr; xr-spatial-tracking; autoplay; camera; microphone"
                      allowfullscreen="true"
                      webkitallowfullscreen="true"
                      mozallowfullscreen="true"
                      id="virtual"
                      className="virtual"
                      style={{
                        height: "calc(100vh - 240px)",
                      }}
                      oallowfullscreen="true"
                      msallowfullscreen="true"
                    ></iframe>
                  ) : (
                    <>
                      <h3 className="text-center mt-5">
                        {t("not_available_prop")}
                      </h3>
                    </>
                  )}
                </div>
              </div>
            </div>
            <div className="overlay-canvas-sidebar"></div>

            {feat.length && (
              <FindAProperty title={t("other_props")} data={feat} />
            )}
          </>
        )}
      </div>
      <Footer />
    </>
  );
};

export default V360;
